/* ===================
Variables
=================== */

:root {
  --mediumGreen: #3ebd5d;
  --darkerGreen: #3aac56;
  --purple: #af34ab;
  --white: #ffffff;
  --lightGreen: rgba(221, 241, 210, 1);
  --lightBlack: rgba(0, 0, 0, 0.8);
  --boxShadow: 2px 5px 3px 0 rgba(0, 0, 0, 0.4);
}

/* ===================
Global Styles
=================== */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: var(--lightBlack);
}

body {
  text-align: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--lightGreen);
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

img {
  width: 100%;
  display: block;
}

/* ===================
Navbar
=================== */

.navbar {
  padding: 1rem 2rem;
  display: flex;
  /* links on the right */
  align-items: center;
  background-color: var(--mediumGreen);
  box-shadow: var(--boxShadow);
}

.nav-links {
  display: flex;
  margin-left: auto;
}

.nav-links a {
  display: inline-block;
  letter-spacing: 1px;
  padding: 1rem;
  font-size: 1.7rem;
  color: var(--white);
  justify-content: space-between;
}

.nav-links a:hover {
  background-color: var(--white);
  border: 2px solid var(--purple);
  color: var(--purple);
}

/* ===================
Buttons
=================== */
button:focus {
  outline: none !important;
  outline-offset: none !important;
  background-color: var(--purple) !important;
  color: var(--white) !important;
}

.btn {
  font-size: 1.2rem;
  border: 2px solid var(--purple);
  color: var(--purple);
  padding: 0.7rem;
  background-color: var(--white);
}

.btn:hover {
  background-color: var(--purple);
  color: var(--white);
}

.btn-categories {
  margin-top: 0.5rem;
  margin-right: 1rem;
  background-color: var(--white);
}

.btn-categories:last-child {
  margin-right: 0;
}

/* ===================
About Page
=================== */

.card-about {
  padding: 3rem 4rem 4rem 4rem;
  background-color: var(--white);
  box-shadow: var(--boxShadow);
}

.card-about h2 {
  color: var(--darkerGreen);
  font-size: 1.8rem;
  margin-bottom: 2rem;
}

.card-about h3 {
  margin-bottom: 2rem;
  font-weight: normal;
  font-size: 1.5rem;
}

.card-about p {
  font-size: 1.3rem;
}

.review {
  border: 2px var(--mediumGreen) solid;
  margin: 4rem auto 0 auto;
  width: 100%;
  padding: 1.3rem 3rem;
  box-shadow: var(--boxShadow);
  text-align: center;
}

.review h2 {
  color: var(--darkerGreen);
  font-size: 1.8rem;
}

.review-img-container {
  position: relative;
  width: 140px;
  height: 140px;
  margin: 2rem auto 1rem auto;
  border: var(--purple) 2px solid;
  border-radius: 50%;
  padding: 0.5rem;
}

.avatar {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.quote-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 3rem;
  height: 3rem;
  background: var(--purple);
  border-radius: 50%;
  display: grid;
  place-items: center;
}

.review-name {
  color: var(--purple);
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

.review-description {
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.btn-prev, .btn-next {
  font-size: 1.2rem;
  background: transparent;
  border-color: transparent;
  margin: 0 1rem;
  cursor: pointer;
}

.btn-prev:hover, .btn-next:hover {
  background: var(--mediumGreen);
  color: var(--white);
}

/* ===================
Books Page
=================== */

.section {
  padding: 4rem 0 5rem 0;
  text-align: center;
}

.title {
  padding-top: 5rem;
  padding-bottom: 3rem;
  color: var(--purple);
  font-size: 3rem;
}

.books-grid {
  width: 70vw;
  display: grid;
  grid-gap: 4rem;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  justify-items: center;
  margin: 0 auto;
}

/* ===================
Book Container
=================== */

.book {
  text-align: center;
  background-color: var(--white);
  box-shadow: var(--boxShadow);
}

.book img {
  object-fit: cover;
  height: 25rem;
}

.book-footer {
  padding: 2rem 1rem 3rem 1rem;
}

.book-footer h2 {
  margin-bottom: 0.5rem;
  color: rgba(39, 24, 24, 0.8);
}

.book-footer p {
  margin-bottom: 2rem;
  color: rgba(39, 24, 24, 0.9);
}

/* ===================
Book Details Page
=================== */

.section-container {
  width: 85vw;
  margin: 0 auto;
  margin-bottom: 5rem;
}

.card {
  padding: 2rem 2rem 3rem 2rem;
  background-color: var(--white);
  box-shadow: var(--boxShadow);
}

.book-img-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.book-img-container img {
  height: auto;
}

.book-info p {
  padding: 0.3rem;
  text-align: left;
  font-size: 1.1rem;
}

.book-description {
  padding-top: 2rem !important;
  padding-bottom: 1rem !important;
}

.book-data-label {
  font-weight: bold;
  font-size: 1.1rem;
}

.store-link {
  margin-top: 3rem;
}

/* ===================
Error Page
=================== */

.error {
  color: rgb(226, 72, 72);
  font-size: 2.2rem;
}

/* ===================
Screen Sizes
=================== */

/* Tablet */

@media screen and (min-width: 768px) and (max-width: 979px) {
  .section-container {
    width: 90vw;
  }
  .card {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 2rem;
  }
  .book-description {
    padding-top: 0 !important;
    padding-bottom: 1rem !important;
  }
  .review {
    width: 90%;
  }
}

/* Small desktop*/

@media screen and (min-width: 980px) and (max-width: 1199px) {
  .section-container {
    width: 80vw;
  }
  .card {
    display: grid;
    grid-template-columns: 2fr 2fr;
    gap: 2rem;
  }
  .book-description {
    padding-top: 0 !important;
    padding-bottom: 1rem !important;
  }
  .review {
    width: 85%;
  }
}

/* Large desktop */

@media screen and (min-width: 1200px) and (max-width: 1599px) {
  .section-container {
    width: 70vw;
  }
  .card {
    display: grid;
    grid-template-columns: 2fr 2fr;
    gap: 2rem;
  }
  .book-description {
    padding-top: 0 !important;
    padding-bottom: 1rem !important;
  }
  .review {
    width: 80%;
  }
}

/* Extra Large desktop */

@media screen and (min-width: 1600px) {
  .section-container {
    width: 60vw;
  }
  .card {
    display: grid;
    grid-template-columns: 2fr 2fr;
    gap: 2rem;
  }
  .book-description {
    padding-top: 0 !important;
    padding-bottom: 1rem !important;
  }
  .review {
    width: 70%;
  }
}